body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Montserrat", sans-serif;
}




.navigation {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: right;
            justify-content: right;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #2a3e55;
    padding: 15px 5px 15px 5px;
    z-index: 9999999999;
    color: #ffffff;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.03) !important;
    border-bottom: 3px solid #dedede;
      border-bottom-width: 3px;
      border-bottom-style: solid;
  }
  .innerNav {
    margin-right: 5%;
    margin-left: 5%;
  
    display: -webkit-flex;
  
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: none;
    padding: 0 15px;
    border-radius: 50px;
    overflow: hidden;
  }
  .stickyNav {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 15px;
    margin-left: 15px;
    cursor: pointer;
  }
  
  .ChangeColor {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    direction: ltr;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    float: left;
    -webkit-flex: auto;
            flex: auto;
    border: 1px solid;
    border-radius: 5px;
    background: none;
    padding: 10px 10px 10px 10px;
  }
  
.fullpage {
    width: 100%;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex: auto;
            flex: auto;
    text-align: center;
  }
.title {
    color: #000;
    font-size: 60px;
  }
  .subtitle {
    color: #000;
    font-size: 25px;
    font-weight: normal;
  }
  .socialicons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .icon {
    padding: 5px 5px 5px 5px;
  }
  
.BackgroundBox {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    max-width: 50%;
    -webkit-align-items: center;
            align-items: center;
    background: linear-gradient(to bottom, #fff, #fff);
    padding: 50px 50px 50px 50px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.075);
}
.aboutcontent {
    font-size: 20px;
    line-height: 1.5;
    text-align: justify;
}
.card {
    background-color: #fff;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 300px;
    height: 320px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.095);
    padding: 20px 20px 20px 20px;
    margin: 20px 20px 20px 20px;
  }
  .icon-wrapper {
    font-size: 40px;
    color: chocolate;
  }
  .skill-title-wrapper h4 {
    font-size: 20px;
    border-top: 1px solid #e9ecef !important;
    padding: 15px;
  }
  .aContactme {
    display: inline-block;
    font-weight: 300;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: 1px solid transparent;
    padding: 0.35rem 1.8rem;
    font-size: 1rem;
    line-height: 1.975;
    border-radius: 10rem !important;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    cursor: pointer;
    box-shadow: 0px 4px 8px #f8af383b;
    color: #fff;
    background: #fdb743 linear-gradient(90deg, #fdb743, #fd9f2f) repeat-x;
    text-decoration: none;
  }
  
@media only screen and (max-width: 1000px) {
.cardWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
  }
}
@media only screen and (min-width: 1001px) {
